.welcome {
    padding-left: 1rem;
    /* text-align: center; */
  }
  
  .form {
    padding: 1rem;
  }
  
  input[type="text"],
  input[type="password"],
  input[type="email"],
  textarea {
    display: block;
    font: inherit;
    border-radius: 4px;
    border: 1px solid #ccc;
    padding: 0.25rem;
    width: 50%;
  }
  
  label {
    display: block;
    font-weight: bold;
  
  }
  
  .container {
    background-color: #f2f2f2;
    border-radius: 6px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); 
    margin: 3rem auto;
    width: 90%;
    max-width: 60rem;
  }
  
  .btnContainer {
    padding-top: 10px;
  }
  
  .incPass {
    border-color: red;
  }